import { useState, useEffect, useRef } from 'react';
import { fabric } from 'fabric';
import { setSize } from '../utils/updateCanvas';
import { loadAndUse } from '../utils/updateObject';

const loadCanvas = (
  canvas,
  canvasInfo,
  shouldLoad,
  isMounted,
  state,
  setCanvas,
  setSpeakerVideos,
  setObjectIdCount
) => {
  canvas.clear();
  let videos = [];
  let lastId = 1;
  canvas.loadFromJSON(canvasInfo, () => {
    let objects = canvas.getObjects();
    let textArr = [];
    for (let i = 0; i < objects.length; i++) {
      let obj = objects[i];
      if (!shouldLoad && obj.type === 'video') canvas.remove(obj);
      else {
        obj.id = lastId;
        lastId++;
        if (obj.type === 'customTextbox') textArr.push(obj);
        else if (obj.type === 'video') videos.push(obj);
      }
    }

    for (let i = 0; i < textArr.length; i++) {
      let text = textArr[i];
      loadAndUse(canvas, text, text.fontFamily);
    }

    if (isMounted.current) {
      setSize(state, canvas);
      setObjectIdCount(lastId);
      setCanvas(canvas);
      if (shouldLoad) setSpeakerVideos(videos);
    }
  });
};

const addCanvas = (
  state,
  info,
  shouldLoad,
  setCanvas,
  isMounted,
  setSpeakerVideos,
  setObjectIdCount
) => {
  let canvas = new fabric.Canvas('canvas', {
    width: state.videoWidth,
    height: state.videoHeight,
    resize: state.resize,
    backgroundColor: 'rgba(255,255,255,1)',
    preserveObjectStacking: true,
    fireRightClick: true,
    stopContextMenu: true,
    selection: false,
  });

  if (info.canvas) {
    loadCanvas(
      canvas,
      info.canvas,
      shouldLoad,
      isMounted,
      state,
      setCanvas,
      setSpeakerVideos,
      setObjectIdCount
    );
  } else {
    setSize(state, canvas);
    setCanvas(canvas);
  }
};

const useAddCanvas = (
  editorState,
  setCanvas,
  info,
  setIsCanvasData,
  speakers,
  setObjectIdCount,
  setSpeakerVideos,
  userAssets
) => {
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const isMounted = useRef(false);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  useEffect(() => {
    if (isFirstLoad && editorState.width > 0 && userAssets) {
      let isCanvas = info && info.canvas && info.canvas.objects.length > 0;
      let shouldLoad = speakers.length === 0 && isCanvas;
      if (shouldLoad) setIsCanvasData(true);
      addCanvas(
        editorState,
        info,
        shouldLoad,
        setCanvas,
        isMounted,
        setSpeakerVideos,
        setObjectIdCount
      );
      setIsFirstLoad(false);
    }
  }, [
    speakers,
    setCanvas,
    editorState,
    isFirstLoad,
    info,
    setIsCanvasData,
    setObjectIdCount,
    setSpeakerVideos,
    userAssets,
  ]);
};

export default useAddCanvas;
