import { useState, useEffect, useRef } from 'react';
import { cacheAssets } from '../../../utils/cacheAssets';
import { getResizeValues } from '../../../utils/canvasUtils';
import { loadAndUse } from '../utils/updateObject';
import { roundedCorners } from '../utils/updateObject';

const finishHandler = (
  canvas,
  lastId,
  videos,
  state,
  template,
  setSpeakerVideos,
  setObjectIdCount,
  dispatch,
  setIsLoaded,
  speakerVideos
) => {
  let currentEl = null;
  for (let i = 0; i < videos.length; i++) {
    let templateEl = videos[i];
    if (speakerVideos[i]) currentEl = speakerVideos[i];
    if (currentEl) {
      let scaleX = templateEl.width / currentEl.width;
      let scaleY = templateEl.height / currentEl.height;
      let scale = Math.max(scaleX, scaleY);
      templateEl.originalWidth = currentEl.originalWidth;
      templateEl.originalHeight = currentEl.originalHeight;
      templateEl.width = currentEl.width;
      templateEl.height = currentEl.height;
      templateEl.scaleX *= scale;
      templateEl.scaleY *= scale;
      templateEl.cropRect = currentEl.cropRect;
      templateEl.src = currentEl.src;
      if (templateEl.cornerRadius !== 0) {
        templateEl.set(
          'clipPath',
          roundedCorners(templateEl, templateEl.cornerRadius)
        );
      }
      templateEl.setCoords();
    } else canvas.remove(templateEl);
  }
  let { width, height, resize } = getResizeValues(
    state.canvasState.initialWidth,
    state.canvasState.initialHeight,
    template.resize
  );
  dispatch({ type: 'setVideoSize', data: { width, height, resize } });
  setObjectIdCount(lastId);
  setSpeakerVideos(videos);
  setIsLoaded(true);
};

const loadCanvas = (
  canvas,
  template,
  isMounted,
  state,
  setSpeakerVideos,
  setObjectIdCount,
  dispatch,
  setIsLoaded,
  speakerVideos
) => {
  canvas.clear();
  let videos = [];
  let lastId = 1;
  canvas.loadFromJSON(template, () => {
    let objects = canvas.getObjects();
    let textArr = [];
    for (let i = 0; i < objects.length; i++) {
      let obj = objects[i];
      obj.id = lastId;
      lastId++;
      if (obj.type === 'customTextbox') textArr.push(obj);
      else if (obj.type === 'video') videos.push(obj);
    }

    for (let i = 0; i < textArr.length; i++) {
      let text = textArr[i];
      loadAndUse(canvas, text, text.fontFamily);
    }

    if (isMounted.current) {
      finishHandler(
        canvas,
        lastId,
        videos,
        state,
        template,
        setSpeakerVideos,
        setObjectIdCount,
        dispatch,
        setIsLoaded,
        speakerVideos
      );
    }
  });
};

const useLoadTemplate = (
  templateToLoad,
  setTemplateToLoad,
  canvas,
  video,
  editorState,
  dispatch,
  setUserAssets,
  setObjectIdCount,
  setSpeakerVideos,
  speakerVideos
) => {
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [assets, setAssets] = useState(null);
  const [isLoaded, setIsLoaded] = useState(null);

  const isMounted = useRef(false);

  useEffect(() => {
    if (isLoaded) {
      setIsLoaded(false);
      dispatch({ type: 'setIsLoadingTemplate', data: false });
    }
  }, [isLoaded, dispatch, canvas]);

  useEffect(() => {
    if (assets) {
      setIsFirstLoad(true);
      setAssets(null);
      setUserAssets(assets);
      loadCanvas(
        canvas,
        templateToLoad,
        isMounted,
        editorState,
        setSpeakerVideos,
        setObjectIdCount,
        dispatch,
        setIsLoaded,
        speakerVideos
      );
      setTemplateToLoad(null);
    }
  }, [
    templateToLoad,
    canvas,
    assets,
    editorState,
    setUserAssets,
    setTemplateToLoad,
    setSpeakerVideos,
    setObjectIdCount,
    dispatch,
    setIsLoaded,
    speakerVideos,
  ]);

  useEffect(() => {
    if (templateToLoad && isFirstLoad) {
      setIsFirstLoad(false);
      dispatch({ type: 'setIsLoadingTemplate', data: true });
      dispatch({ type: 'setIsSubtitles', data: false });
      video.pause();
      canvas.clear();
      let assets = templateToLoad.objects.filter(
        (obj) => obj.type === 'customImage'
      );
      cacheAssets(assets, setAssets, isMounted);
    }
  }, [templateToLoad, canvas, dispatch, video, isFirstLoad]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);
};

export default useLoadTemplate;
